const Main = (resolve) => {
	import ('@/components/main/index').then((module) => {
		resolve(module)
	})
}

const ParentView = (resolve) => {
	import ('@/components/parent-view/index').then((module) => {
		resolve(module)
	})
}
/**
 * iview-admin中meta除了原生参数外可配置的参数:
 * meta: {
 *  title: { String|Number|Function }
 *         显示在侧边栏、面包屑和标签栏的文字
 *         使用'{{ 多语言字段 }}'形式结合多语言使用，例子看多语言的路由配置;
 *         可以传入一个回调函数，参数是当前路由对象，例子看动态路由和带参路由
 *  hideInBread: (false) 设为true后此级路由将不会出现在面包屑中，示例看QQ群路由配置
 *  breadCanTurnTo: false  设为true后在面包屑中可以跳转
 *  hideInMenu: (false) 设为true后在左侧菜单不会显示该页面选项
 *  notCache: (false) 设为true后页面在切换标签后不会缓存，如果需要缓存，无需设置这个字段，而且需要设置页面组件name属性和路由配置的name一致
 *  access: (null) 可访问该页面的权限数组，当前路由设置的权限会影响子路由
 *  icon: (-) 该页面在左侧菜单、面包屑和标签导航处显示的图标，如果是自定义图标，需要在图标名称前加下划线'_'
 *  beforeCloseName: (-) 设置该字段，则在关闭当前tab页时会去'@/router/before-close.js'里寻找该字段名对应的方法，作为关闭前的钩子函数
 * }
 */
export default [{
	path: '/',
	name: '_home',
	redirect: '/home',
	component: Main,
	meta: {
		hideInBread: true,
		icon: 'icon-home',
		notCache: true,
		title: "主页"
	},
	children: [{
		path: '/home',
		name: 'home',
		meta: {
			title: "首页",
			notCache: true,
			icon: 'icon-home'
		},
		component: () =>
			import ('@/view/home/home')
	}, {
		path: '/home1',
		name: 'home1',
		meta: {
			title: "首页1",
			notCache: true,
			icon: 'icon-home'
		},
		component: () =>
			import ('@/view/home/home1')
	}, {
		path: '/home2',
		name: 'home2',
		meta: {
			title: "首页2",
			notCache: true,
			icon: 'icon-home'
		},
		component: () =>
			import ('@/view/home/home2')
	}]
}, {
	path: '/components',
	name: 'components',
	component: Main,
	meta: {
		icon: 'icon-detail',
		title: '组件'
	},
	children: [{
		path: 'component1',
		name: 'component1',
		meta: {
			icon: 'icon-detail',
			title: "组件1"
		},
		component: () =>
			import ('@/view/components/component1')
	}, {
		path: 'component2',
		name: 'component2',
		meta: {
			icon: 'icon-detail',
			title: '组件2',
		},
		component: () =>
			import ('@/view/components/component2')
	}]

}, {
	path: '/pages',
	name: 'pages',
	component: Main,
	meta: {
		icon: 'icon-package',
		title: '页面'
	},
	children: [{
		path: 'page1',
		name: 'page1',
		meta: {
			icon: 'icon-package',
			title: '页面1',
			beforeCloseName: 'before_close_normal'
		},
		component: () =>
			import ('@/view/pages/page1')
	}, {
		path: 'page2',
		name: 'page2',
		meta: {
			icon: 'icon-package',
			title: '页面2'
		},
		component: () =>
			import ('@/view/pages/page2')
	}, {
		path: 'page3',
		name: 'page3',
		meta: {
			icon: 'icon-package',
			title: '页面3'
		},
		component: () =>
			import ('@/view/pages/page3')
	}]
}, {
	path: '/multilevel',
	name: 'multilevel',
	meta: {
		icon: 'icon-zhankaicaidan',
		title: '多级菜单'
	},
	component: Main,
	children: [{
		path: 'level_1',
		name: 'level_1',
		meta: {
			icon: 'icon-detail',
			title: '二级_1'
		},
		component: () =>
			import ('@/view/multilevel/level1.vue')
	}, {
		path: 'level_2',
		name: 'level_2',
		meta: {
			// access: ['super_admin'],
			icon: 'icon-detail',
			// showAlways: true,
			title: '二级_2'
		},
		component: ParentView,
		children: [{
			path: 'level_2_1',
			name: 'level_2_1',
			meta: {
				icon: 'icon-detail',
				title: '二级_2_1'
			},
			component: () =>
				import ('@/view/multilevel/level-2/level-2-1.vue')
		}, {
			path: 'level_2_2',
			name: 'level_2_2',
			meta: {
				icon: 'icon-detail',
				title: '二级_2_2'
			},
			component: () =>
				import ('@/view/multilevel/level-2/level-2-1.vue')
		}]
	}, {
		path: 'level_3',
		name: 'level_3',
		meta: {
			icon: 'icon-detail',
			title: '三级_1'
		},
		component: () =>
			import ('@/view/multilevel/level1.vue')
	}]
}, {
	path: '*',
	name: 'error_404',
	meta: {
		hideInMenu: true
	},
	component: () =>
		import ('@/view/error-page/404.vue')
}]


